html {
    box-sizing: border-box;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: inherit;
  }

  p,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
  }

  a {
    text-decoration: none;
  }
  
  ul,
  ol {
    list-style: none;
    padding: 0;
    margin: 0;
  }

 button {
    cursor: pointer;

  }
  address {
    font-style: normal;
  }

  img {
    display: block;
   }